.form-field {
   $parent: &;
   position: relative;
   &__input {
      width: 100%;
      height: 60px;
      padding: 0 20px 0 54px;
      @include f('ub400', 14, 24);
      border-radius: 30px;
      background-color: $white;
      border: 1px solid #e6e6e6;
      transition: .3s ease-in-out;
      @include placeholder-color($color5);
      color: $color2;
      @include xs {
         padding: 0 20px 0 48px;
         height: 50px; }
      &:focus {
         border-color: #cf0110;
         ~ #{$parent}__icon svg {
             fill:  #cf0110; } }
      //#{$parent}.has-error &
 }      // border-color: rgba(213, 29, 72, 1)

   &__icon {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 40px;
      color: #3198f0;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      font-size: 10px;
      @include xs {
         width: 40px; }
      svg {
         position: relative;
         top: 1px;
         transition: .3s ease-in-out; }
      #{$parent}_message & {
         bottom: auto;
         height: 60px; } } }
//#{$parent}.has-error &
// color: rgba(213, 29, 72, 1)

.textarea {
   $parent: &;
   display: block;
   width: 100%;
   height: 150px;
   padding: 18px 20px 18px 55px;
   resize: none;
   background-color: $white;
   border-radius: 30px;
   border: 1px solid #e6e6e6;
   @include f('ub400', 14, 24);
   transition: .3s ease-in-out;
   @include placeholder-color($color5);
   color: $color2;
   &:focus {
      border-color: #cf0110;
      ~ .form-field__icon svg {
         fill:  #cf0110; } } }

.form-submit-btn {
   cursor: pointer;
   input {
      display: none; } }

.check-box {
   $parent: &;
   &.is-disabled {
      pointer-events: none; }
   &__label {
      display: inline-flex;
      align-items: flex-start;
      cursor: pointer; }

   &__square {
      position: relative;
      width: 12px;
      height: 12px;
      flex: 0 0 12px;
      margin-top: 7px;
      border-radius: 4px;
      border: 1px solid #808080;
      #{$parent}_type-1 & {
         margin-right: 20px; }
      #{$parent}_type-2 & {
         margin-right: 10px; }
      #{$parent}.is-disabled & {
         opacity: .3; }
      #{$parent}_radio & {
         &::after {
            position: absolute;
            top: calc(50% - 3px);
            left: calc(50% - 3px);
            opacity: 0;
            transform: scale(0);
            width: 6px;
            height: 6px;
            background-color: $color1;
            border-radius: 50%;
            transition: .3s ease-in-out;
            content: ''; } } }
   &__icon {
      opacity: 0;
      transform: scale(0);
      position: absolute;
      top: -1px;
      left: 2px;
      transition: .3s ease-in-out;
      font-size: 10px;
      fill: $color1;
      #{$parent}_radio & {
         display: none; } }
   &__text {
      @include f('ub400', 14, 24);
      #{$parent}_type-2 & {
         color: $color5; } }
   &__input {
      display: none;
      &:checked ~ #{$parent}__square #{$parent}__icon,
      &:checked ~ #{$parent}__square::after {
         opacity: 1;
         transform: scale(1); } } }


// *** begin contact-form *** //
.contact-form {
   $parent: &;
   position: relative;
   padding: 35px 40px 45px;
   box-shadow: 0 15px 40px rgba(26, 3, 4, 0.1);
   background-color: $white;
   text-align: center;
   &__title {
      position: relative;
      margin-bottom: 20px;
      padding-bottom: 10px;
      @include f('ub700', 26, 34);
      color: $color2;
      &::before {
         position: absolute;
         left: calc(50% - 2.5px);
         bottom: -2px;
         z-index: 1;
         width: 5px;
         height: 5px;
         background-color: $color1;
         border-radius: 50%;
         content: ''; }
      &::after {
         position: absolute;
         bottom: 0;
         left: calc(50% - 35px);
         width: 70px;
         height: 1px;
         background-color: $color1;
         opacity: 0.1;
         content: ''; }
      span {
         @include f('ub300'); } }
   &__row {
      #{$parent}_contacts & {
         margin: 0 -10px; }
      &:not(:last-child) {
         margin-bottom: 30px; } }
   &__field {
      flex: 1 1 calc(50% - 30px);
      width: calc(50% - 30px);
      margin: 0 15px;
      #{$parent}_contacts & {
         margin: 0 10px; } }
   &__submit-btn {
      #{$parent}_contacts & {
         flex: 1 1 calc(50% - 20px);
         width: calc(50% - 20px);
         min-width: 240px; } }
   &__dots {
      @include coverdiv;
      pointer-events: none;
      &::before, &::after {
         position: absolute;
         top: 15px;
         width: 4px;
         height: 4px;
         border-radius: 50%;
         background-color: $color1;
         content: ''; }
      &::before {
         left: 15px; }
      &::after {
         right: 15px; }
      &-el {
         &::before, &::after {
            position: absolute;
            bottom: 15px;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: $color1;
            content: ''; }
         &::before {
            left: 15px; }
         &::after {
            right: 15px; } } } }

// end contact-form //
