body {
   min-width: 320px;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   @include f('ub400', 16, 24);
   color: $color3;
   &.is-locked {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: 0;
      overflow: hidden; } }

.container {
   position: relative;
   z-index: 1;
   max-width: 1170px;
   margin: 0 auto;
   padding: 0 15px; }

.row {
   display: flex;
   margin: 0 -15px; }

.col {
   padding: 0 15px; }

.main {
   overflow: hidden;
   padding-top: 130px; }

.grid {
   &__sizer {
      float: left; } }

.section {
   $parent: &;
   position: relative;
   &_screen {
      z-index: 1; }
   &_advantages {
      z-index: 2; }
   &_faq, &_config, &_contacts {
      z-index: 3; }
   &_complex {
      z-index: 2; }
   &__line {
      position: absolute;
      z-index: 1;
      top: 0;
      bottom: 0;
      width: 1px;
      pointer-events: none;
      &::before, &::after {
         position: absolute;
         width: 1px;
         top: 0;
         bottom: 0;
         content: ''; }
      &_l {
         left: 50%;
         margin-left: -391px;
         &::before {
            transform: translateX(-389px); }
         &::after {
            transform: translateX(-195px); }
         #{$parent}_advantages &,
         #{$parent}_complex &,
         #{$parent}_contacts & {
            top: -23px;
            @include r(1599) {
               top: -32px; }
            @include md {
               top: -40px; }
            &::after {
               display: none; } }
         #{$parent}_complex & {
            &::before {
               top: -59px!important; } }

         #{$parent}_faq &,
         #{$parent}_config & {
            bottom: 27px;
            &::before {
               top: -58px;
               bottom: 74px; }
            &::after {
               top: -25px;
               bottom: 33px; } } }

      &_c {
         left: 50%;
         margin-left: -4px;
         &::before {
            transform: translateX(-194px); }
         &::after {
            transform: translateX(194px); }
         #{$parent}_advantages & {
            top: 15px;
            &::before, &::after {
               display: none; } }
         #{$parent}_complex &,
         #{$parent}_contacts & {
            &::before, &::after {
               display: none; } }
         #{$parent}_solutions &,
         #{$parent}_equip & {
            background-color: $black;
            opacity: 0.06;
            &::before, &::after {
               display: none; } } }

      &_r {
         right: 50%;
         margin-right: -391px;
         &::before {
            transform: translateX(193px); }
         &::after {
            transform: translateX(390px); }
         #{$parent}_advantages &,
         #{$parent}_complex &,
         #{$parent}_contacts & {
            top: -23px;
            @include r(1599) {
               top: -32px; }
            @include md {
               top: -40px; }
            &::before {
               display: none; } }
         #{$parent}_faq &,
         #{$parent}_config & {
            bottom: 27px;
            &::after {
               top: -58px;
               bottom: 74px; }
            &::before {
               top: -25px;
               bottom: 33px; } } }

      #{$parent}_screen &,
      #{$parent}_faq &,
      #{$parent}_config & {
         opacity: .06;
         background: $white;
         &::before, &::after {
            background: $white; } }
      #{$parent}_advantages &,
      #{$parent}_complex &,
      #{$parent}_contacts & {
         opacity: .06;
         background: $black;
         &::before, &::after {
            background: $black;
            top: -60px; } }
      #{$parent}_faq &,
      #{$parent}_config & {
         top: -22px; }
      #{$parent}_footer & {
         top: 80px;
         opacity: .06;
         background: $black; } } }


.section-title {
   $parent: &;
   position: relative;
   &::before {
      position: absolute;
      width: 3px;
      content: ''; }
   &_left {
      padding-left: 20px;
      text-align: left;
      &::before {
         top: 14px;
         left: -15px;
         bottom: 5px;
         background-color: $color1;
         content: ''; } }
   &_center {
      text-align: center;
      &::before {
         left: calc(50% - 5px);
         bottom: -40px;
         width: 3px;
         height: 25px;
         background-color: $color1;
         content: ''; } }
   &_lg {
      padding-left: 12px;
      &::before {
         top: 15px;
         left: -15px;
         bottom: 15px; } }
   &__inner {
      position: relative;
      display: inline-flex;
      #{$parent}_center & {
         justify-content: center; }
      #{$parent}_left & {
         justify-content: flex-start; } }
   &__icon {
      position: absolute;
      top: -9px;
      left: -15px;
      flex-shrink: 0;
      z-index: -1;
      #{$parent}_lg & {
         top: -12px; } }
   &__text {
      @include f('ub700', 40, 50);
      text-transform: uppercase;
      #{$parent}_lg & {
         font-size: 52px;
         line-height: 62px; }

      #{$parent}_black & {
         color: $color2; }
      #{$parent}_white & {
         color: $white; }
      span {
         @include f('ub300'); } }
   &_lg span {
      margin-left: 0; }
   &__descr {
      color: $color3;
      @include f('ub300', 22, 34);
      &_color-1 {
         color: #e6e6e6; }
      &_color-2 {
         color: #e5e5e5; } } }

.title {
   @include f('ub700', 16, 24);
   text-transform: uppercase;
   &_black {
      color: $color2; }
   &_white {
      color: $white; } }

strong {
   @include f('ub500'); }

.btn {
   position: relative;
   display: inline-block;
   padding: 16px 42px;
   outline: 0;
   @include f('ub500', 16, 24);
   text-align: center;
   border-radius: 28px;
   cursor: pointer;
   transition: .1s ease-in-out;
   &::after, &::before {
      @include coverdiv;
      border-radius: 28px;
      content: ''; }
   span {
      position: relative;
      z-index: 1; }
   &_red {
      color: $white;
      &:hover {
         &::before {
            background-color: lighten($color1, 10); } }
      &:active {
         transform: translateY(5px);
         &::after {
            box-shadow: none;
            transform: translateY(0); } }
      &::after {
         transform: translateY(5px);
         background-color: $color7;
         box-shadow: 0 0 48px 3px rgba(240, 67, 79, 0.2);
         transition: .1s ease-in-out; }
      &::before {
         z-index: 1;
         background-color: $color1;
         transition: .3s ease-in-out; } } }

.arc-decor {
   $parent: &;
   position: absolute;
   width: 100%;
   z-index: 2;
   bottom: 0;
   height: 150px;
   overflow: hidden;
   pointer-events: none;
   @include rmin(1950) {
      height: 200px; }
   &__mask {
      position: absolute;
      bottom: 0;
      right: 0;
      left: 0;
      width: 100%;
      height: auto;
      &_top {
         #{$parent}_color-1 & {
            fill: $color8; }
         #{$parent}_color-2 & {
            fill: $color8;
            opacity: .1; }
         #{$parent}_color-3 & {
            fill: $color8;
            opacity: .5; } }

      &_bot {
         #{$parent}_color-1 &,
         #{$parent}_color-2 & {
             fill: $white; }
         #{$parent}_color-3 & {
            fill: #ebebed; } } }
   &__noise {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 119px;
      background: url("../img/decor/arc_noise.png");
      @include cover;
      @include rmin(1950) {
         height: 160px; } } }

.noise-bg {
   $parent: &;
   position: relative;
   background: #17171a url(../img/decor/noise_black.png);
   &::before, &::after {
      position: absolute;
      z-index: 1;
      background-repeat: no-repeat;
      content: ''; }
   &_type-1 {
      &::before {
         top: 11%;
         left: 0;
         width: 808px;
         height: 803px;
         background-image: url("../img/decor/tire-tracks_l.png"); }
      &::after {
         top: 10.5%;
         right: 0;
         width: 491px;
         height: 633px;
         background-image: url("../img/decor/tire-tracks_r.png"); } }
   &_type-2 {
      &::before {
         left: 0;
         bottom: 22%;
         width: 255px;
         height: 579px;
         background-image: url("../img/decor/gear_l.png"); }
      &::after {
         right: 0;
         bottom: 22%;
         width: 290px;
         height: 579px;
         background-image: url("../img/decor/gear_r.png"); } } }
