.slider {
   display: none;
   &.slick-initialized {
      display: block; } }

.slider-controls {
   position: absolute;
   @include flexCenter;
   height: 20px;
   z-index: 1;
   &_gray .slider-btn {
      svg {
         fill: #808080; } }
   &_black .slider-btn {
      svg {
         fill: $color2; } }
   &__count {
      position: absolute;
      color: $color2;
      @include f('ub700', 18, 18);
      opacity: .1;
      transform: translateX(0);
      transition: .3s ease-in-out;
      &_l {
         left: -35px; }
      &_r {
         right: -35px; }
      &.is-disabled {
         opacity: 0;
         transition: none; }
      .is-left & {
         opacity: 0;
         transform: translateX(10px); }
      .is-right & {
         opacity: 0;
         transform: translateX(-10px); } } }

.slider-dots {
   $parent: &;
   display: flex;
   justify-content: center;
   line-height: 0;
   &_gray {
      button {
         background-color: #808080; } }
   &_black {
      button {
         background-color: $color2; } }
   li {
      &:not(:last-child) {
         margin-right: 24px; }
      &.slick-active {
         button {
            pointer-events: none;
            background-color: $color1; } }
      button {
         width: 6px;
         height: 6px;
         border-radius: 50%;
         @include hide-text;
         transition: .3s ease-in-out;
         &:hover {
            opacity: .5; } } } }

.slider-btn {
   position: absolute;
   top: 0;
   transition: .3s ease-in-out;
   &:hover {
      opacity: .5; }
   &_prev {
      left: 0; }
   &_next {
      right: 0; }
   svg {
      font-size: 22px; }
   &.slick-disabled {
      pointer-events: none;
      opacity: .15; } }
